<template>
  <div>
    <ModalView :is_open_modal="isOpenModal" @close="isOpenModal = false">
      <h1>학년 선택하기</h1>
      <div style="padding-top: 5%"/>
      <div class="grade-view" v-for="i in max_grade" :key="i" @click="setGrade(i)">
        <p>{{i}}</p>
      </div>
    </ModalView>

    <div class="input_modal" @click="open_modal">
      <p v-if="grade">{{grade}}학년</p>
      <p v-else>학년</p>
    </div>
  </div>
</template>

<script>
import ModalView from "@/components/ModalView";
export default {
  name: "InputGrade",
  components: {
    ModalView
  },
  data() {
    return {
      max_grade: 3,
      isOpenModal: false,
    }
  },
  computed: {
    grade () {
      return this.$store.state.setting_grade
    }
  },
  methods: {
    open_modal: function () {
      this.isOpenModal = !this.isOpenModal

      if( this.$store.state.setting_school.SCHUL_KND_SC_NM === '초등학교')this.max_grade = 6
      else this.max_grade = 3
    },
    setGrade: function (g) {
      this.$store.state.setting_grade = g
      this.open_modal()
    }
  },
}
</script>

<style scoped>

.input_modal {
  margin-top: 3px;
  margin-left: 5%;
  width: 90%;
  border: #bdbdbd solid 2px;
  border-radius: 10px;
  height: 50px;
}

.input_modal p {
  margin-top: 16px;
  margin-left: 10px;
}

.grade-view p {
  top: -73px;
  font-size: 15px;
}
.grade-view h6 {
  position: relative;
  top: -34px;
  font-size: 16px;
}
.grade-view {
  margin-top: 5px;
  border: #bdbdbd solid 2px;
  border-radius: 10px;
  margin-left: 15%;
  width: 70%;
  height: 50px;
}

</style>