<template>
  <div>
    <ModalView :is_open_modal="isOpenModal" @close="isOpenModal = false">
      <h1>학교 선택하기</h1>
      <input type="text" class="schoolnm" placeholder="학교" @input="search_school">
      <div class="school_view" v-for="(i,j) in searched_schools" :key="j" @click="select_school(i)">
        <h6>{{i.SCHUL_NM}}</h6>
        <p>{{i.ORG_RDNMA}}</p>
      </div>
    </ModalView>

    <div class="input_modal" @click="open_modal">
      <p v-if="selected_school">{{ selected_school.SCHUL_NM }}</p>
      <p v-else>학교</p>
    </div>

  </div>
</template>

<script>
import ModalView from "@/components/ModalView";
export default {
  name: "InputSchool",
  components: {
    ModalView
  },

  data() {
    return {
      isOpenModal: false,
      searched_schools: undefined
    }
  },
  computed: {
    selected_school () {
      return this.$store.state.setting_school
    }
  },
  methods: {
    select_school: function (sc_obj) {
      this.$store.state.setting_school = sc_obj
      this.open_modal()
    },
    open_modal: function () {
      this.isOpenModal = !this.isOpenModal
    },
    search_school: function (name) {
      fetch('https://open.neis.go.kr/hub/schoolInfo?' + new URLSearchParams({Type: 'json', SCHUL_NM: name.target.value}))
      .then(data => data.json())
      .then((res) => {
        console.log(name.target.value)
        this.searched_schools = res['schoolInfo'][1]['row']

      })
    }
  }
}
</script>

<style scoped>

.school_view p {
  position: relative;
  top: -68px;
  font-size: 15px;
}
.school_view h6 {
  position: relative;
  top: -33px;
  font-size: 16px;
}
.school_view {
  margin-top: 5px;
  border: #bdbdbd solid 2px;
  border-radius: 10px;
  margin-left: 15%;
  width: 70%;
  height: 50px;
}

.modal input{
  margin-top: 0;
  padding-left: 8px;
  width: 70%;
  height: 32px;
  font-size: 15px;
  border: 0;
  border-radius: 5px;
  outline: none;
  background-color: #d3d2d2
}

.input_modal {
  margin-top: 6%;
  margin-left: 5%;
  width: 90%;
  border: #bdbdbd solid 2px;
  border-radius: 10px;
  height: 50px;
}

.input_modal p {
  margin-top: 16px;
  margin-left: 10px;
}
</style>