<template>
  <div>
    <ModalView :is_open_modal="isOpenModal" @close="isOpenModal = false">
      <h1>반 선택하기</h1>
      <div style="padding-top: 5%"/>
      <div style="height: 60%; overflow: auto;">
        <div class="class-view" v-for="i in max_class" :key="i" @click="setClass(i)">
          <p>{{i}}</p>
        </div>
      </div>
    </ModalView>
    <div class="input_modal" @click="open_modal">
      <p v-if="sc_class">{{sc_class}}반</p>
      <p v-else>반</p>
    </div>
  </div>
</template>

<script>
import ModalView from "@/components/ModalView";
export default {
  name: "InputClass",
  components: {
    ModalView
  },
  data() {
    return {
      max_class: 0,
      isOpenModal: false
    }
  },
  computed: {
    sc_class () {
      return this.$store.state.setting_class
    }
  },
  methods: {
    open_modal: function () {
      console.log('https://open.neis.go.kr/hub/classInfo?' + new URLSearchParams({
        Type: 'json',
        KEY: 'a7d9b63eda43415e8802a55fa0f4f2d2',
        ATPT_OFCDC_SC_CODE: this.$store.state.setting_school.ATPT_OFCDC_SC_CODE,
        SD_SCHUL_CODE: this.$store.state.setting_school.SD_SCHUL_CODE,
        AY: new Date().getFullYear(),
        GRADE: this.$store.state.setting_grade
      }))
      fetch('https://open.neis.go.kr/hub/classInfo?' + new URLSearchParams({
        Type: 'json',
        KEY: 'a7d9b63eda43415e8802a55fa0f4f2d2',
        ATPT_OFCDC_SC_CODE: this.$store.state.setting_school.ATPT_OFCDC_SC_CODE,
        SD_SCHUL_CODE: this.$store.state.setting_school.SD_SCHUL_CODE,
        AY: new Date().getFullYear(),
        GRADE: this.$store.state.setting_grade
      }))
          .then(data => data.json())
          .then((res) => {
            let maxClass = 0
            res['classInfo'][1]['row'].forEach(element => {
              maxClass = Math.max(element.CLASS_NM, maxClass)
            })
            this.max_class = maxClass
            this.isOpenModal = !this.isOpenModal
          })
    },
    setClass: function (c) {
      this.$store.state.setting_class = c
      this.isOpenModal = false
    }
  }
}
</script>

<style scoped>
.input_modal {
  margin-top: 3px;
  margin-left: 5%;
  width: 90%;
  border: #bdbdbd solid 2px;
  border-radius: 10px;
  height: 50px;
}

.input_modal p {
  margin-top: 14px;
  margin-left: 10px;
}

.class-view p {
  top: -73px;
  font-size: 15px;
}
.class-view h6 {
  font-size: 16px;
}
.class-view {
  margin-top: 5px;
  border: #bdbdbd solid 2px;
  border-radius: 10px;
  margin-left: 15%;
  width: 70%;
  height: 50px;
}
</style>