<template>
  <div class="setting">
    <div class="go_back">
      <p @click="$router.back()"> 뒤로가기 </p>
    </div>
    <div class="top_title">
      <h2>학교 설정</h2>
      <p>학급 시간표와 급식 정보를 제공하기 위한</p>
      <p>필수 정보를 입력해주세요.</p>
    </div>
    <InputSchool/>
    <InputGrade/>
    <InputClass/>
    <SaveButton/>
  </div>
</template>

<script>
import InputSchool from "@/components/InputSchool";
import InputGrade from "@/components/InputGrade";
import InputClass from "@/components/InputClass";
import SaveButton from "@/components/SaveButton";
import { useCookies } from "vue3-cookies";
export default {
  name: "SettingView",
  components: {
    InputSchool,InputGrade,InputClass,SaveButton
  },
  setup () {
    const { cookies } = useCookies();
    return { cookies };
  },
  created() {
    this.$store.state.setting_school = this.cookies.get('setting_school')
    this.$store.state.setting_grade = this.cookies.get('setting_grade')
    this.$store.state.setting_class = this.cookies.get('setting_class')
  },
  data() {
    return {
      selected_school: undefined,
      selected_grade: 0,
      selected_class: 0,
    }
  },
  methods: {

  }
}
</script>

<style scoped>
.top_title{
  margin-left: 5%;
}
.go_back {
  width: 100%;
  height: 30px;
  border-bottom: #c1c1c1 solid 1px;
}
</style>