<template>
 <div class="time_table" v-if="schedule">
   <div class="blank" style="height: 20px"/>
   <div class="info">
     <p>시간표</p>
   </div>
   <div class="real">
     <div class="sero" v-for="i in class_num" :key="i">
       <div class="garo" v-for="j in 6" :key="j">
         <p v-if="i === 1"> {{ week[j-1] }} </p>
         <p v-else-if="j === 1">{{i-1}}교시</p>
         <p v-else>{{ schedule[j-1][i-1].replaceAll('-','') }}</p>
       </div>
     </div>
   </div>
 </div>
</template>

<script>
import {useCookies} from "vue3-cookies";

export default {
  name: "TimeTable",
  data () {
    return {
      schedule: undefined,
      class_num: 8,
      week: ['', '월', '화', '수', '목', '금', '']
    }
  },
  setup() {
    const { cookies } = useCookies()
    return { cookies }
  },
  created() {
    const school = this.cookies.get('setting_school')
    if( school.SCHUL_KND_SC_NM === '초등학교')this.setElementarySchool()
    else this.setMiddleSchool()
  },
  methods: {
    getWeek () {
      const currentDay = new Date();
      const theYear = currentDay.getFullYear();
      const theMonth = currentDay.getMonth();
      const theDate = currentDay.getDate();
      const theDayOfWeek = currentDay.getDay();

      const thisWeek = [];
      {
        const resultDay = new Date(theYear, theMonth, theDate + (1 - theDayOfWeek));
        const yyyy = resultDay.getFullYear();
        let mm = Number(resultDay.getMonth()) + 1;
        let dd = resultDay.getDate();
        mm = String(mm).length === 1 ? '0' + mm : mm;
        dd = String(dd).length === 1 ? '0' + dd : dd;
        thisWeek[0] = yyyy + '' + mm + '' + dd;
      }
      {
        const resultDay = new Date(theYear, theMonth, theDate + (5 - theDayOfWeek));
        const yyyy = resultDay.getFullYear();
        let mm = Number(resultDay.getMonth()) + 1;
        let dd = resultDay.getDate();
        mm = String(mm).length === 1 ? '0' + mm : mm;
        dd = String(dd).length === 1 ? '0' + dd : dd;
        thisWeek[1] = yyyy + '' + mm + '' + dd;
      }
      return thisWeek
    },
    parse (str) {
      const y = str.substr(0, 4),
          m = str.substr(4, 2) - 1,
          d = str.substr(6, 2);
      return new Date(y,m,d);
    },
    setMiddleSchool () {
      const school = this.cookies.get('setting_school')
      const thisweek = this.getWeek()
      fetch('https://open.neis.go.kr/hub/misTimetable?' + new URLSearchParams({
        KEY: 'a7d9b63eda43415e8802a55fa0f4f2d2',
        Type: 'json',
        ATPT_OFCDC_SC_CODE: school.ATPT_OFCDC_SC_CODE,
        SD_SCHUL_CODE: school.SD_SCHUL_CODE,
        GRADE: this.cookies.get('setting_grade'),
        CLASS_NM: this.cookies.get('setting_class'),
        TI_FROM_YMD: thisweek[0],
        TI_TO_YMD: thisweek[1],
      })).then(data => data.json())
          .then(res => {
            this.schedule = Array.from(Array(10), () => new Array(10).fill(''))
            res['misTimetable'][1]['row'].forEach(element => {
              this.schedule[this.parse(element.ALL_TI_YMD).getDay()][element.PERIO] = element.ITRT_CNTNT
            })
          })
    },
    setElementarySchool () {
      const school = this.cookies.get('setting_school')
      const thisweek = this.getWeek()
      fetch('https://open.neis.go.kr/hub/elsTimetable?' + new URLSearchParams({
        KEY: 'a7d9b63eda43415e8802a55fa0f4f2d2',
        Type: 'json',
        ATPT_OFCDC_SC_CODE: school.ATPT_OFCDC_SC_CODE,
        SD_SCHUL_CODE: school.SD_SCHUL_CODE,
        GRADE: this.cookies.get('setting_grade'),
        CLASS_NM: this.cookies.get('setting_class'),
        TI_FROM_YMD: thisweek[0],
        TI_TO_YMD: thisweek[1],
      })).then(data => data.json())
          .then(res => {
            this.schedule = Array.from(Array(10), () => new Array(10).fill(''))
            res['elsTimetable'][1]['row'].forEach(element => {
              this.schedule[this.parse(element.ALL_TI_YMD).getDay()][element.PERIO] = element.ITRT_CNTNT
            })
          })
      console.log('https://open.neis.go.kr/hub/elsTimetable?' + new URLSearchParams({
        KEY: 'a7d9b63eda43415e8802a55fa0f4f2d2',
        Type: 'json',
        ATPT_OFCDC_SC_CODE: school.ATPT_OFCDC_SC_CODE,
        SD_SCHUL_CODE: school.SD_SCHUL_CODE,
        GRADE: this.cookies.get('setting_grade'),
        CLASS_NM: this.cookies.get('setting_class'),
        TI_FROM_YMD: thisweek[0],
        TI_TO_YMD: thisweek[1],
      }))
    }
  }
}
</script>

<style scoped>
.real {
  margin-left: 1%;
  width: 98%;
}

.time_table {
  height: 100%;
  background-color: #ace8eb;
}
.sero {
  display: flex;
  justify-content: space-between;
}
.garo {
  text-align: center;
  width: 15%;
  height: 50px;
  margin: 3px;

  background-color: white;
  border: 1px solid white;
  border-radius: 10px;
}
</style>