<template>
  <div>
    <div class="save" @click="save">
      <p>저장하기</p>
    </div>
  </div>
</template>

<script>
import { useCookies } from "vue3-cookies";

export default {
  name: "SaveButton",
  setup() {
    const { cookies } = useCookies();
    return { cookies };
  },
  methods: {
    save () {
      this.cookies.set("setting_school",this.$store.state.setting_school,'30d')
      this.cookies.set("setting_grade",this.$store.state.setting_grade,'30d')
      this.cookies.set("setting_class",this.$store.state.setting_class,'30d')

      this.$router.push('/schedule')
    }
  }
}
</script>

<style scoped>
.save {
  text-align: center;
  margin-left: 5%;
  border-radius: 10px;
  position: absolute;
  right: 0;
  bottom: 2%;
  left: 0;
  height: 50px;
  width: 90%;
  background-color: #3282f6;
}

.save p {
  margin-top: 14px;
  margin-left: 10px;

  color: white;
}
</style>